function Connexion() {
  const [show, setShow] = useState(false);
  const [message, setMessage] = useState("");
  const [email, setEmail] = useState("");
  const [statut, setStatut] = useState("");
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  const addNewPreInscription = () => {
    const data = {
      email: email,
      statut: statut,
    };
    const headers = new Headers({
      "Content-Type": "application/json",
    });

    const options = {
      method: "POST",
      body: JSON.stringify(data),
      headers: headers,
    };
    fetch(
      "https://back-end.osc-fr1.scalingo.io/vitrine/preInscription",
      options
    )
      .then((response) => {
        return response.json();
      })

      .then(
        (responseData) => {
          if (responseData.message === "serveur") {
            let link = " https://serveur.tipourboire.com/inscription";
            window.location.href = link;
          } else {
            let link = " https://restaurant.tipourboire.com/inscription";
            window.location.href = link;
          }
        },

        (error) => {
          console.log(error);
        }
      );
  };

  return (
    <>
      <Button className="buttonPreInscr" onClick={handleShow}>
        Je m'inscris{" "}
        <Button onClick={handleShow} className="flecheInscr">
          {">"}
        </Button>
      </Button>

      <Modal
        show={show}
        onHide={handleClose}
        animation={true}
        backdrop="static"
        keyboard={false}>
        <Modal.Header closeButton>
          <Modal.Title>Pré-inscription</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          {/*<img className="imgModal" src="/images/LogoOK.png" />*/}

          <Row>
            <Col className="colBenef" md={6} lg={6}>
              <a href="https://serveur.tipourboire.com/inscription">
                <img className="logoPopup" src="/images/LogoOk.png" />
              </a>

              <Button
                href="https://serveur.tipourboire.com/inscription"
                className="inscriptionModal">
                Je suis bénéficiaire du Tipourboire
              </Button>
            </Col>
            <Col className="colProprio" md={6} lg={6}>
              <a href="https://restaurant.tipourboire.com/inscription">
                <img className="logoPopup" src="/images/LogoOk.png" />
              </a>
              <Button
                href="https://restaurant.tipourboire.com/inscription"
                className="inscriptionModal">
                Je suis propriétaire d'un établissement
              </Button>
            </Col>
          </Row>
        </Modal.Body>
      </Modal>
    </>
  );
}

