class NameContainer extends Component {
  render() {
    return (
      <div>
        {this.props.questions.map((name) => (
          <Name name={name} />
        ))}
      </div>
    );
  }
}

class Name extends Component {
  render() {
    return <div>{this.props.name}</div>;
  }
}

